<template>
    <div>
        <!-- Table All Member -->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="member" >
            <template slot="header" class="mb-5">
                <h1 class="mr-2">Data Member</h1>
                <vx-tooltip text="Tambah Member" style="display:inline">
                    <vs-button @click="addMember" radius icon="create" color="success" type="flat" size="large"></vs-button>
                </vx-tooltip>
            </template>

            <template slot="thead">
                <vs-th sort-key="nama"  class="bg-primary text-white">
                Nama
                </vs-th>
                <vs-th sort-key="tipe" width="130px" class="bg-primary text-white">
                Status
                </vs-th>
                <vs-th sort-key="tipe" width="130px" class="bg-primary text-white">
                Wilayah
                </vs-th>
                <vs-th sort-key="aksi"  width="160px" class="bg-primary text-white text-center">
                <p class="w-full">Aksi</p>
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.nama">
                        {{items.nama}}
                    </vs-td>
                    <vs-td :data="items.tipe">
                        <div v-if="items.tipe == 'STOCKPOINT' ">
                            <vs-chip color="success">
                                STOCKPOINT
                            </vs-chip>
                        </div>
                        <div v-else>
                        {{items.tipe}}
                        </div>
                    </vs-td>
                    <vs-td :data="items.wilayah">
                        {{items.wilayah}}
                    </vs-td>
                    <vs-td>
                        <!-- <vx-tooltip text="Ubah Member" style="display:inline">
                            <vs-button @click="editData(items.kode, items.nama, items.tipe, items.wilayah)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                        </vx-tooltip> -->
                        <vx-tooltip text="Foto Member" style="display:inline">
                            <vs-button @click="popupAddImage(items.kode)" class="mr-1" color="danger" type="filled" size="small" icon="image" style="display:inline"></vs-button>
                        </vx-tooltip>
                        <vx-tooltip text="Ringkasan Produk" style="display:inline">
                            <vs-button  @click="popupSummaryBtn(items.kode, items.nama, items.tipe,)" class="mr-1" color="success" type="filled" size="small" icon="poll" style="display:inline"></vs-button>
                        </vx-tooltip>
                        <div v-show="items.tipe == 'STOCKPOINT' " style="display:inline"  >
                        <vx-tooltip text="Tambah Stok" style="display:inline">
                            <vs-button  @click="addNewStock(items.kode, items.nama, items.tipe,)"  color="#0fabbc" type="filled" size="small" icon="system_update_alt" style="display:inline"></vs-button>
                        </vx-tooltip>
                        </div>
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Add Member -->
        <vs-popup class="holamundo"  title="Tambah Member" :active.sync="addNew">
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input class="w-full" label="Nama" v-validate="'required'" name="form.nama" data-vv-validate-on="blur" v-model="form.nama" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input type="number" class="w-full" label="NIK" v-validate="'required'" name="form.nik" data-vv-validate-on="blur" v-model="form.nik" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input class="w-full" label="NPWP (Boleh Di Kosongkan)" v-validate="'required'" name="form.npwp" data-vv-validate-on="blur" v-model="form.npwp" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input type="number" class="w-full" label="No Handphone" v-validate="'required'" name="form.hp" data-vv-validate-on="blur" v-model="form.hp" />
                </div>
            </div>
            <div class="vx-row mb-3">
                <div class="vx-col w-full">
                <small class="ml-1">Alamat</small><br>
                <vs-textarea class="w-full" v-validate="'required'" name="form.alamat" data-vv-validate-on="blur" v-model="form.alamat" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <small class="ml-1">Tipe</small><br>
                <v-select :placeholder="'-- Pilih Tipe --'" label="label" :reduce="tipe => tipe.code" :options="tipe" v-model="form.tipe" ></v-select>
                </div>
            </div>
            <div class="vx-row mb-6" v-show="form.tipe == 'PR' || form.tipe == 'R' ">
                <div class="vx-col w-full">
                <small class="ml-1">Stockpoint</small><br>
                <v-select :placeholder="'-- Pilih Stockpoint --'" :reduce="stockpoint => stockpoint.kode" label="nama" :options="stockpoint" v-model="form.kode_stockpoint"  ></v-select>
                </div>
            </div>
            <div class="vx-row mb-6" v-show="form.tipe == 'S' ">
                <div class="vx-col w-full">
                <small class="ml-1">Wilayah</small><br>
                <v-select :placeholder="'-- Pilih Wilayah --'" :reduce="wilayah => wilayah.kode" label="nama" :options="wilayah" v-model="form.kode_wilayah"  ></v-select>
                </div>
            </div>
            <div class="vx-row">
                <div class="vx-col w-full text-right">
                <vs-button color="success" class="mr-3 mb-2" :disabled="!validateFormAdd" @click="addData()">Simpan</vs-button>
                </div>
            </div>
        </vs-popup>

        <!-- Edit Member -->
        <vs-popup class="holamundo"  title="Edit Member" :active.sync="Edit">
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input class="w-full" label="Nama" v-validate="'required'" name="form.nama" data-vv-validate-on="blur" v-model="form.nama" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input type="number" class="w-full" label="NIK" v-validate="'required'" name="form.nik" data-vv-validate-on="blur" v-model="form.nik" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input class="w-full" label="NPWP (Boleh Di Kosongkan)" v-validate="'required'" name="form.npwp" data-vv-validate-on="blur" v-model="form.npwp" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <vs-input type="number" class="w-full" label="No Handphone" v-validate="'required'" name="form.hp" data-vv-validate-on="blur" v-model="form.hp" />
                </div>
            </div>
            <div class="vx-row mb-3">
                <div class="vx-col w-full">
                <small class="ml-1">Alamat</small><br>
                <vs-textarea class="w-full" v-validate="'required'" name="form.almat" data-vv-validate-on="blur" v-model="form.alamat" />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <small class="ml-1">Tipe</small><br>
                <v-select :placeholder="'-- Pilih Tipe --'" label="label" :reduce="tipe => tipe.code" :options="tipe" v-model="form.tipe" ></v-select>
                </div>
            </div>
            <div class="vx-row mb-6" v-show="form.tipe == 'PR' || form.tipe == 'R' ">
                <div class="vx-col w-full">
                <small class="ml-1">Stockpoint</small><br>
                <v-select :placeholder="'-- Pilih Stockpoint --'" :reduce="stockpoint => stockpoint.kode" label="nama" :options="stockpoint" v-model="form.kode_stockpoint"  ></v-select>
                </div>
            </div>
            <div class="vx-row mb-6" v-show="form.tipe == 'S' ">
                <div class="vx-col w-full">
                <small class="ml-1">Wilayah</small><br>
                <v-select :placeholder="'-- Pilih Wilayah --'" :reduce="wilayah => wilayah.kode" label="nama" :options="wilayah" v-model="form.kode_wilayah"  ></v-select>
                </div>
            </div>
            <div class="vx-row">
                <div class="vx-col w-full text-right">
                <vs-button color="success" class="mr-3 mb-2" :disabled="!validateFormAdd" @click="addData()">Simpan</vs-button>
                </div>
            </div>
        </vs-popup>

        <!-- Add Foto Member -->
        <vs-popup class="holamundo"  title="Foto Member" :active.sync="popupImage">
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <small class="ml-1">Foto KTP</small><br>
                    <img class="img-ktp my-2" :src="formImage.ktp">
                    <vs-input type="file" @change="handleImageKtp" accept="image/*"  />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <small class="ml-1">Foto Selfie</small><br>
                    <img class="img-selfie my-2" :src="formImage.selfie">
                    <vs-input type="file" @change="handleImageSelfie" accept="image/*"  />
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                <small class="ml-1">Foto Freezer</small><br>
                    <img class="img-freezer my-2" :src="formImage.freezer">
                    <vs-input type="file" @change="handleImageFreezer" accept="image/*"  />
                </div>
            </div>
            <div class="vx-row">
                <div class="vx-col w-full text-right">
                <vs-button color="success" class="mr-3 mb-2" @click="addImage()">Simpan</vs-button>
                </div>
            </div>
        </vs-popup>

        <!-- Tambah Stock -->
        <vs-popup fullscreen title="Tambah Stok" :active.sync="addStock">
            <vx-card>
            <vs-table stripe search  max-items="1000" :data="products">
            <template slot="header" class="mb-5">
                <div class="vx-row mb-6">
                    <div class="vx-col sm:w-24 w-full">
                        <span>Invoice</span>
                    </div>
                    <div class="vx-col">
                        <vs-input v-model="form_stock.invoice"  v-validate="'required'" name="form_stock.invoice"  data-vv-validate-on="blur" placeholder="INV1239898"/>
                        <vs-input hidden v-model="form_stock.kode_stockpoint" />
                    </div>
                </div>
            </template>
            <template slot="thead">
                <vs-th sort-key="sku" width="150px" class="bg-primary text-white">
                Sku
                </vs-th>
                <vs-th sort-key="barang"  class="bg-primary text-white">
                Nama
                </vs-th>
                <vs-th sort-key="harga_stockpoint"  width="150px"  class="bg-primary text-white">
                Stok
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode_barang" v-for="items in data" >
                    <vs-td :data="items.sku">
                        {{items.sku}}
                    </vs-td>
                    <vs-td :data="items.barang">
                        {{items.barang}}
                    </vs-td>
                    <vs-td>
                        <vs-input v-validate="'required'" name="items.qty"  data-vv-validate-on="blur" class="w-full" v-model="items.qty" />
                    </vs-td>
                </vs-tr>
            </template>
            </vs-table>
            </vx-card>
            <div class="vx-row">
                <div class="vx-col w-full text-right">
                <vs-button color="success" class="mt-10" :disabled="!validateFormAddStock" @click="addStockPoint()">Simpan</vs-button>
                </div>
            </div>
        </vs-popup>

         <!-- Popup Data Summary -->
        <vs-popup fullscreen title="Ringkasan Produk" :active.sync="popupDataSummary">
            <!-- Card -->
            <div class="vx-row">
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-success),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-shopping-cart text-success font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.pembelian_blnini}}</h2>
                                    <span>Pembelian</span>
                                </div>
                            </div>
                        </div> 
                    </div>
                </div>   
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-danger),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-truck text-danger font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.terjual_blnini}}</h2>
                                    <span>Penjualan</span>
                                </div>
                            </div> 
                        </div>
                    </div>
                </div>   
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-warning),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-box text-warning font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.sisa}}</h2>
                                    <span>Sisa</span>
                                </div>
                            </div>
                        </div> 
                    </div>
                </div>   
            </div>
            
            <!-- Table Data Summary -->
            <vx-card>
            <vs-table stripe search pagination  max-items="10" :data="dataSummary">
                 <template slot="header" class="mb-5">
                    <h1 class="mr-2">{{form.nama}} ({{form.tipe}})</h1>
                </template>

                <template slot="thead">
                    <vs-th sort-key="sku" width="120px" class="bg-primary text-white">
                    Sku
                    </vs-th>
                    <vs-th sort-key="barang" class="bg-primary text-white">
                    Barang
                    </vs-th>
                    <vs-th sort-key="qty" width="80px"  class="bg-primary text-white">
                    Qty
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :key="items.kode" v-for="items in data" >
                        <vs-td :data="items.sku">
                            {{items.sku}}
                        </vs-td>
                        <vs-td :data="items.barang">
                            {{items.barang}}
                        </vs-td>
                        <vs-td>
                            {{items.qty}}
                        </vs-td>
                    </vs-tr>
                </template>
            </vs-table>
            </vx-card>
        </vs-popup>
    </div>
</template>

<script>
export default {
    data() {
        return {
            addNew:false,
            Edit:false,
            addStock:false,
            popupImage:false,
            popupDataSummary:false,
            member:[],
            products:[],
            wilayah:[],
            dataSummary:[],
            tipe:[
                {label: 'Stockpoint', code: 'S'},
                {label: 'Penggerak Reseller', code: 'PR'},
                {label: 'Reseller', code: 'R'}
            ],
            stockpoint:[],
            form: {
                nama: "",
                alamat: "",
                nik: "",
                npwp: "",
                hp: "",
                tipe: "",
                kode_stockpoint: "",
                kode_wilayah:''
            },
            form_stock: {
                invoice:'',
                kode_stockpoint:'',
                kode_barang:'',
                qty: ''
            },
            formImage: {
                kode:'',
                ktp:'',
                selfie:'',
                freezer:''
            },
            card:{
                pembelian_blnini:'',
                terjual_blnini:'',
                pembelian_blnlalu:'',
                terjual_blnlalu:'',
                sisa:''
            }

        }

    },

    computed: {
        validateFormAdd() {
        return !this.errors.any() 
            && this.form.nama != '' 
            && this.form.alamat != ''
            && this.form.nik != ''
            && this.form.hp != ''
            && this.form.tipe != ''
        },
        validateFormAddStock() {
        return !this.errors.any() 
            && this.form_stock.invoice != '' 
        },
    },

    created() {
       this.loadData();
       this.loadStockpoint();
       this.loadProduct();
       this.loadWilayah();
    },

    methods:{
        loadData(){
            this.$vs.loading();
            this.$http.get('agen/web/member')
            .then(response => {
                this.member = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        loadWilayah(){
            this.$http.get('agen/web/wilayah')
            .then(response => {
                this.wilayah = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        loadStockpoint(){
            this.$http.get('agen/web/member/stockpoint')
            .then(response => {
                this.stockpoint = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        loadProduct(){
            this.$vs.loading();
            this.$http.get('agen/web/produk')
            .then(response => {
                // this.products = response.data.message
                this.products = JSON.parse(JSON.stringify(response.data.message), function(k, v) {
                    if (k === "qty_po") 
                        this.qty = v;
                    else if (k === "kode")
                        this.kode_barang = v;
                    else
                        return v;
                });
            // console.log(this.products);
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
            
            
        },

        addMember(){
            this.addNew = true,
            this.form.nama = '',
            this.form.alamat = '',
            this.form.nik = '',
            this.form.npwp = '',
            this.form.hp = '',
            this.form.tipe = '',
            this.form.kode_stockpoint =''
            this.form.kode_wilayah =''
        },

        addData(){
            this.$vs.loading();
            const postData = {
                nama: this.form.nama,
                alamat: this.form.alamat,
                nik: this.form.nik,
                npwp: this.form.npwp,
                hp: this.form.hp,
                tipe: this.form.tipe,
                kode_stockpoint: this.form.kode_stockpoint,
                kode_wilayah: this.form.kode_wilayah,
            }

            this.$http.post('agen/web/member/create', postData)
            
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.form.nama = '',
                    this.form.alamat = '',
                    this.form.nik = '',
                    this.form.npwp = '',
                    this.form.hp = '',
                    this.form.tipe = '',
                    this.form.kode_stockpoint = null
                    this.form.wilayah = null
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                        this.loadStockpoint();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        addNewStock(kode){
            this.addStock = true
            this.form_stock.kode_stockpoint = kode
            this.form_stock.invoice =''
            this.form_stock.qty =''
        },

        addStockPoint(){

            const postData = {
                invoice: this.form_stock.invoice,
                kode_stockpoint: this.form_stock.kode_stockpoint,
                keranjang: this.products    
            }

            this.$http.post('agen/web/member/stok/input', postData)
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.addStock = false,
                    this.form_stock.invoice = ''
                    this.form_stock.kode_stockpoint = ''
                    this.products = []

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',
                    showConfirmButton: false
                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        handleImageKtp(e){
            const selectedImage = e.target.files[0];
            this.createBase64ImageKtp(selectedImage);
        },

        createBase64ImageKtp(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.formImage.ktp = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        handleImageSelfie(e){
            const selectedImage = e.target.files[0];
            this.createBase64ImageSelfie(selectedImage);
        },

        createBase64ImageSelfie(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.formImage.selfie = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        handleImageFreezer(e){
            const selectedImage = e.target.files[0];
            this.createBase64ImageFreezer(selectedImage);
        },

        createBase64ImageFreezer(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.formImage.freezer = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        popupAddImage(kode){
            this.$vs.loading();
            this.popupImage = true
            this.formImage.kode = kode

            this.$http.get('agen/web/member/foto/'+ kode)
            .then(response=> {
                this.formImage.ktp = response.data.message[0].ktp
                this.formImage.selfie = response.data.message[0].selfie
                this.formImage.freezer = response.data.message[0].freezer
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        addImage(){
            this.$vs.loading();
            const postData = {
                kode: this.formImage.kode,
                ktp: this.formImage.ktp,
                selfie: this.formImage.selfie,
                freezer:this.formImage.freezer
            }

            this.$http.post('agen/web/member/foto/upload', postData)
                
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.formImage.ktp = '',
                    this.formImage.selfie = '',
                    this.formImage.freezer = '',
                    this.popupImage = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }  
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        popupSummaryBtn(kode, nama, tipe){
            this.$vs.loading();
            this.popupDataSummary = true
            this.form.nama = nama
            this.form.tipe = tipe

            this.$http.get('agen/web/member/summary/'+ kode)
            .then(response=> {
                this.dataSummary = response.data.message.table
                this.card.pembelian_blnini = response.data.message.card.pembelian_blnini
                this.card.terjual_blnini = response.data.message.card.terjual_blnini
                this.card.pembelian_blnlalu = response.data.message.card.pembelian_blnlalu
                this.card.terjual_blnlalu = response.data.message.card.terjual_blnlalu
                this.card.sisa = response.data.message.card.sisa
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        }

    }

}
</script>

